/** by default its night theme [8:00 pm, 8:00 am) */
:root {
    /** layouts/bg colors */
    --bg-primary: #333333; /* ! try-out = #222629 */
    --bg-secondary: #444444;
    --bg-footer: #222222;
    --bg-blur: rgba(51, 51, 51, 0.6);
    --img-bg-blur: rgba(51, 51, 51, 0.4);
    /** text colors */
    --text-main: #c4b37f;
    --text-mainopaque: #c4b37f1c; /** 6% opacity of main */
    --text-heading: #dadada;
    --text-submain: #a8a8a8;
    --text-content: #bfbfbf;
    --text-footer: #bbbbbb;
}
/** morning theme [8:00 am, 12:00 noon) */
[data-theme="morning"] {
    --bg-primary: #ffffff;
    --bg-secondary: #eff6ff;
    --bg-footer: #c4b37f;
    --bg-blur: rgba(255, 255, 255, 0.6);
    --img-bg-blur: rgba(240, 240, 240, 0.4);
    --text-main: #c4b37f;
    --text-mainopaque: #c4b37f1c;
    --text-heading: #333333;
    --text-submain: #8d96b2;
    --text-content: #333333;
    --text-footer: #111111;
}
/** evening theme [12.00 pm, 8.00 pm) */
[data-theme="evening"] {
    --bg-primary: #ffffff;
    --bg-secondary: #eff6ff;
    --bg-footer: #0065a3;
    --bg-blur: rgba(255, 255, 255, 0.6);
    --img-bg-blur: rgba(240, 240, 240, 0.4);
    --text-main: #0065a3;
    --text-mainopaque: #0065a31c;
    --text-heading: #333333;
    --text-submain: #8d96b2;
    --text-content: #333333;
    --text-footer: #eeeeee;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

/** when content on page is selected by long-press or mouse-select */
/* ::selection {
    color: var();
    background: var();
} */

body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* -------------- Custom Scroller -------------- */
html {
    -webkit-scrollbar-width: thin;
    -moz-scrollbar-width: thin;
    -ms-scrollbar-width: thin;
    scrollbar-width: thin;
}
body::-webkit-scrollbar {
    width: 8px;
    border-radius: 100%;
}
body::-webkit-scrollbar-track {
    background: var(--bg-primary);
}
body::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 10px;
}

/* ---------- Fonts ----------- */
/** gotu
 ** @used-for logo
 ** @weights normal (400);
 */

/** source-serif-pro
 ** @used-for heading/main-text/title
 ** @weights normal (400); semibold (600); bold (700)
 */

/** arimo
 ** @used-for subtitle
 ** @weights normal (400);
 */

/** manrope
 ** @used-for general content
 ** @weights normal (400); medium (500); bold (700);
 */

/* -------------- Glassmorphism -------------- */
#bg-blur-low {
    background: var(--bg-blur);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
}
#bg-blur-medium {
    background: var(--bg-blur);
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
}
#bg-blur-high {
    background: var(--bg-blur);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
}

/* ------- Class pool to handle tailwind classes ------- */
@layer components {
    /* can add a class for transition from one color-theme to another smoothly */
    .bg-primary-color {
        @apply bg-primary;
    }
    .bg-secondary-color {
        @apply bg-secondary;
    }
    .bg-footer-color {
        @apply bg-footer-bg;
    }

    .text-main-color {
        @apply text-main;
    }
    .text-heading-color {
        @apply text-heading;
    }
    .text-submain-color {
        @apply text-submain;
    }
    .text-content-color {
        @apply text-content;
    }
    .text-footer-color {
        @apply text-footer;
    }

    .container-padding-x-dir {
        @apply mx-auto px-8 xs:px-10 sm:px-12 md:px-14 lg:px-16;
    }
    .container-padding-y-dir {
        @apply py-10 sm:py-12 md:py-14 lg:py-20;
    }

    .section-size {
        @apply min-h-full max-w-7xl mx-auto;
    }

    .box-border-md {
        @apply border-2 border-main rounded-md;
    }

    .headline {
        @apply text-main-color font-ssp font-semibold lg:font-bold text-lg md:text-xl lg:text-2xl text-center lg:text-left;
    }
    /* lg:font-bold */
    .heading {
        @apply text-heading-color font-ssp font-semibold text-base lg:text-lg;
    }
    .submain {
        @apply text-submain-color font-arimo font-normal text-xs lg:text-sm;
    }
    .content {
        @apply text-content-color font-manrope font-norm-med leading-6 lg:leading-7 lg:tracking-wide-wider text-justify text-sm lg:text-base;
    }
    .aboutMe {
        @apply text-heading-color font-normal;
    }
}
